import { keywordDatabase } from './Keywords';
export const calculate = (inputName, inputKeywords) => {
  if (!inputName) {
    inputName = '';
  }
  if (!inputKeywords) {
    inputKeywords = [];
  }
  // Create an empty unit.
  let outputUnit = {
    name: inputName,
    class: '',
    type: '',
    attack: 0,
    shot: 0,
    shotRange: 0,
    shotRandom: false,
    hit: 0,
    armour: 7,
    command: 0,
    commandRange: 0,
    size: 3,
    cost: 0,
    rules: [],
  };
  // Create an empty array to store rules into later.
  let outputKeywords = [];
  // Prepare the data funnels.
  const updateOuputUnit = (databaseElement) => {
    if (databaseElement.supertype) {
      outputUnit.class = databaseElement.supertype;
    }
    if (databaseElement.type) {
      outputUnit.type = databaseElement.type;
    }
    if (databaseElement.attack) {
      outputUnit.attack += databaseElement.attack;
    }
    if (databaseElement.shot) {
      outputUnit.shot += databaseElement.shot;
    }
    if (databaseElement.shotRange) {
      outputUnit.shotRange += databaseElement.shotRange;
    }
    if (databaseElement.shotRandom) {
      outputUnit.shotRandom = databaseElement.shotRandom;
    }
    if (databaseElement.hit) {
      outputUnit.hit += databaseElement.hit;
    }
    if (databaseElement.armour) {
      outputUnit.armour = databaseElement.armour;
    }
    if (databaseElement.command) {
      outputUnit.command = databaseElement.command;
    }
    if (databaseElement.commandRange) {
      outputUnit.commandRange += databaseElement.commandRange;
    }
    if (databaseElement.size) {
      outputUnit.size = databaseElement.size;
    }
    if (databaseElement.cost) {
      outputUnit.cost += databaseElement.cost;
    }
    if (databaseElement.rules) {
      outputKeywords.push(...databaseElement.rules);
    }
  };
  // Search each database for any entries that match the keywords, apply all math using updateOutputUnit(), temporarily store all special rules into outputKeywords[].
  inputKeywords.map((inputElement) => (
    <>
      {keywordDatabase.classes.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.types.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.attacks.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.shots.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.shotRanges.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.hits.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.armours.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.commands.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.rules.map((databaseElement) => (databaseElement.name === inputElement ? outputKeywords.push(databaseElement.name) : ''))}
    </>
  ));
  // Trim any duplicate abilities before applying to the final object.
  const updateOutputs = (keywords) => {
    const cleanList = [...new Set(keywords)];
    cleanList.map((cleanListElement) => keywordDatabase.rules.map((databaseElement) => (databaseElement.name === cleanListElement ? (outputUnit.cost += databaseElement.cost) : '')));
    outputUnit.rules = cleanList;
  };
  updateOutputs(outputKeywords);
  // Apply final points adjustments.
  // Too powerful? More than 1 strike adds a points tax.
  let strikes = 0;
  if (outputUnit.class === 'Single Stand Unit') {
    strikes += outputUnit.attack > 2 ? 1 : 0;
    strikes += outputUnit.hit > 4 ? 1 : 0;
    strikes += outputUnit.armour < 6 ? 1 : 0;
  } else {
    strikes += outputUnit.attack > 3 ? 1 : 0;
    strikes += outputUnit.hit > 3 ? 1 : 0;
    strikes += outputUnit.armour < 6 ? 1 : 0;
  }
  if (strikes === 2) {
    outputUnit.cost += 25;
  }
  if (strikes > 2) {
    outputUnit.cost += 45;
  }
  // Way too powerful? More than 1 flag adds another points tax.
  let flags = 0;
  if (outputUnit.class === 'Full Unit') {
    flags += outputUnit.attack > 4 ? 1 : 0;
    flags += outputUnit.hit > 3 ? 1 : 0;
    flags += outputUnit.armour < 5 ? 1 : 0;
  }
  if (flags > 1) {
    outputUnit.cost += 20;
  }
  // Not powerful enough? Add reduce points to all that apply.
  if (outputUnit.armour > 4 && outputUnit.class === 'Full Unit' && outputUnit.type === 'Cavalry') {
    // Light cavalry discount
    outputUnit.cost -= 15;
  }
  if (strikes > 1 && outputUnit.class === 'Full Unit' && outputUnit.type === 'Infantry') {
    // Line infantry discount
    outputUnit.cost -= 25;
  }
  if (outputUnit.shotRange < 50 && outputUnit.shotRange > 15 && outputUnit.class === 'Full Unit') {
    // Short-ranged discount
    outputUnit.cost -= 5;
  }
  if (outputUnit.type === 'Artillery' && outputUnit.class === 'Single Stand Unit' && outputUnit.hit < 3) {
    // Weak artillery discount
    outputUnit.cost -= 15;
  }
  // Hot math fixes.
  if (outputUnit.hit > 5 && outputUnit.type === 'Monster' && outputUnit.class === 'Single Stand Unit') {
    // Huge monster discount
    outputUnit.cost -= 25;
  }

  // Tell the beautiful person to have a great day
  return outputUnit;
};
