import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiFlyingFlag } from 'react-icons/gi';
import { keywordDatabase } from '../databases/Keywords';
const iconStyle = { marginBottom: '0.2em' };

const UnitCard = ({ unit, index, open }) => {
  return (
    <>
      <div className='card border-white mb-2'>
        <div className='card-header bg-transparent d-flex px-0'>
          <button className='form-select btn text-left flex-grow-1 collapsed' data-toggle='collapse' data-target={'#num' + index + '-collapse'}>
            {unit.name} <small className='text-muted'>/ {unit.cost} pts</small>
          </button>
        </div>
        <div className={open ? 'list-group list-group-flush collapsed' : 'list-group list-group-flush collapse'} id={'num' + index + '-collapse'}>
          <div className='list-group-item list-group-item-light d-flex'>
            <small className='flex-grow-1'>
              {unit.class === 'Character' ? 'Single ' + unit.type.toLowerCase() + ' character.' : ''}
              {unit.class === 'Single Stand Unit' ? 'Single stand ' + unit.type.toLowerCase() + ' unit.' : ''}
              {unit.type === 'Artillery' && unit.class === 'Full Unit' ? 'Two stand artillery unit.' : unit.class === 'Full Unit' ? 'Three stand ' + unit.type.toLowerCase() + ' unit.' : ''}
            </small>
            <small>Warmaster Pocket</small>
          </div>
          <li className='list-group-item'>
            {unit.attack ? (
              <small className='pr-1'>
                (<GiMailedFist style={iconStyle} /> {unit.attack})
              </small>
            ) : (
              ''
            )}
            {unit.shot ? (
              <small className='px-1'>
                (<GiPocketBow style={iconStyle} /> {unit.shot}
                {unit.shotRandom ? 'd6' : ''} /<small className='ml-1 text-muted'>{unit.shotRange}cm</small>)
              </small>
            ) : (
              ''
            )}
            {unit.command ? (
              <small className='px-1'>
                (<GiFlyingFlag style={iconStyle} /> {unit.command} /<small className='ml-1 text-muted'>{unit.commandRange}cm</small>)
              </small>
            ) : (
              ''
            )}
            {unit.hit ? (
              <small className='px-1'>
                (<GiHeartPlus style={iconStyle} /> {unit.hit})
              </small>
            ) : (
              ''
            )}
            {unit.armour === 7 ? (
              ''
            ) : unit.armour ? (
              <small className='px-1'>
                (<GiCheckedShield style={iconStyle} /> {unit.armour}+)
              </small>
            ) : (
              ''
            )}
          </li>
          {unit.rules
            ? unit.rules.map((e) =>
                keywordDatabase.rules.map((f) =>
                  e === f.name ? (
                    <li className='list-group-item'>
                      <small className='card-text'>
                        <strong>{f.name}.</strong> {f.text}
                      </small>
                    </li>
                  ) : (
                    ''
                  )
                )
              )
            : ''}
        </div>
      </div>
    </>
  );
};
export default UnitCard;
