import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiFlyingFlag } from 'react-icons/gi';
import { useState } from 'react';
import { calculate } from '../databases/Calculate';
import { keywordDatabase } from '../databases/Keywords';
import UnitCard from './UnitCard';

const iconStyle = { marginBottom: '0.2em' };

const Calculator = () => {
  const [unit, setUnit] = useState({});

  const handleFormChoice = () => {
    const uName = document.getElementById('Name').value;
    const uClass = document.getElementById('Class').value;
    const type = document.getElementById('Type').value;
    const attacks = document.getElementById('Attacks').value;
    const shots = document.getElementById('Shots').value;
    const range = document.getElementById('Range').value;
    const hits = document.getElementById('Hits').value;
    const armour = document.getElementById('Armour').value;
    const command = document.getElementById('Command').value;
    const rule1 = document.getElementById('Rule #1').value;
    const rule2 = document.getElementById('Rule #2').value;
    const rule3 = document.getElementById('Rule #3').value;
    const rule4 = document.getElementById('Rule #4').value;
    const rule5 = document.getElementById('Rule #5').value;
    const rule6 = document.getElementById('Rule #6').value;
    const keywords = [uName, uClass, type, attacks, shots, range, hits, armour, command, rule1, rule2, rule3, rule4, rule5, rule6];
    const newUnit = calculate(uName, keywords);
    setUnit(newUnit);
  };

  const handleTypeChoice = () => {
    document.getElementById('Attacks').selectedIndex = 0;
    document.getElementById('Shots').selectedIndex = 0;
    document.getElementById('Range').selectedIndex = 0;
    document.getElementById('Hits').selectedIndex = 0;
    document.getElementById('Armour').selectedIndex = 0;
    document.getElementById('Command').selectedIndex = 0;
    document.getElementById('Rule #1').selectedIndex = 0;
    document.getElementById('Rule #2').selectedIndex = 0;
    document.getElementById('Rule #3').selectedIndex = 0;
    document.getElementById('Rule #4').selectedIndex = 0;
    document.getElementById('Rule #5').selectedIndex = 0;
    document.getElementById('Rule #6').selectedIndex = 0;
    handleFormChoice();
  };

  const handleClassChoice = () => {
    document.getElementById('Type').selectedIndex = 0;
    handleTypeChoice();
  };

  return (
    <>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Name</span>
        <input type='text' className='form-control' placeholder='Custom Name' id='Name' onChange={(e) => handleFormChoice()} />
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Class</span>
        <select className='form-select' id='Class' onChange={(e) => handleClassChoice()}>
          <option value=''></option>
          {keywordDatabase.classes.map((e) => (
            <option value={e.name}>{e.name}</option>
          ))}
        </select>
      </div>
      <div className={unit.class ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Type</span>
        <select className='form-select' id='Type' onChange={(e) => handleTypeChoice()}>
          <option value=''></option>
          {keywordDatabase.types.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Attacks</span>
        <select className='form-select' id='Attacks' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.attacks.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Shots</span>
        <select className='form-select' id='Shots' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.shots.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Range</span>
        <select className='form-select' id='Range' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.shotRanges.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? (unit.class !== 'Character' ? 'input-group mb-3' : 'input-group mb-3 d-none') : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Hits</span>
        <select className='form-select' id='Hits' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.hits.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? (unit.class !== 'Character' ? 'input-group mb-3' : 'input-group mb-3 d-none') : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Armour</span>
        <select className='form-select' id='Armour' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.armours.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? (unit.class === 'Character' ? 'input-group mb-3' : 'input-group mb-3 d-none') : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Command</span>
        <select className='form-select' id='Command' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.commands.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Rule #1</span>
        <select className='form-select' id='Rule #1' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.rules.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Rule #2</span>
        <select className='form-select' id='Rule #2' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.rules.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Rule #3</span>
        <select className='form-select' id='Rule #3' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.rules.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Rule #4</span>
        <select className='form-select' id='Rule #4' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.rules.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className='input-group mb-3 d-none'>
        <span className='input-group-text col-3 bg-light'>Rule #5</span>
        <select className='form-select' id='Rule #5' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.rules.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className='input-group mb-3 d-none'>
        <span className='input-group-text col-3 bg-light'>Rule #6</span>
        <select className='form-select' id='Rule #6' onChange={(e) => handleFormChoice()}>
          <option value=''></option>
          {keywordDatabase.rules.map((e) => e.class.map((f) => (f === unit.class ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>

      <h3 className='text-center lead mt-5'>Your Custom Unit</h3>
      <UnitCard unit={unit} index={1} open={true} />
    </>
  );
};

export default Calculator;
