const Navigation = ({ setPage }) => {
  return (
    <div className='collapse navbar-collapse' id='navbarContent'>
      <div className='navbar-nav'>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Home')}>
          Home
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Intro')}>
          What is Warmaster?
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Armies')}>
          Armies
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Units')}>
          Units
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Calculator')}>
          Points Calculator
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Rules')}>
          Rules Catalogue
        </button>
        <button className='btn nav-link bg-light mb-1' data-toggle='collapse' data-target='.navbar-collapse.show' onClick={() => setPage('Credits')}>
          Credits
        </button>
      </div>
    </div>
  );
};

export default Navigation;
